/* Element Selectors */
body {
  height: 100%;
  background-color: #066CAF;
  background: radial-gradient(#308fcf 33%, #066CAF);
  background-repeat: no-repeat;
  background-attachment: fixed;
  font-family: "AvenirNextLTPro", verdana, arial, sans-serif;
}

/* Class, Attribute, and Pseudo Selectors */
.title {
  width: 100%;
  text-align: center;
  color: whitesmoke;
  border-bottom: 1px solid black;
}

.title h2 {
  text-shadow: 0 -1px 0 black;
}

.doctors {
  margin-top: 35px;
}

.doctors .padding-doctor {
  width: 49%;
  display: inline-block;
  box-sizing: border-box;
  min-width: 320px;
  min-height: 50px !important;
  overflow: hidden;
  height: 105px;
}

.padding-doctor .doctor-card {
  border-radius: 90px 90px 90px 90px;
  background-color: #368ECA;
  width: 75%;
  min-width: 320px;
  max-width: 460px;
  margin: 0 auto;
  height: 103px;
  position: relative;
  box-shadow: inset 1px 1px 5px 0px white, inset -2px -2px 5px #5EB0E6, 1px 1px 5px #2F2D2D;
}

.border-image {
  width: 80px;
  height: 80px;
  overflow: hidden;
  border-radius: 50%;
  position: relative;
  top: 10%;
  left: 4%;
  border-style: solid;
  border-color: white;
  /*  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  background-position: center center;
  background-image: url('https://pbs.twimg.com/profile_images/2650210446/c8ccb28dd7492c586fb38e438121c404_400x400.jpeg');*/
}

.doctor-card .doctor-name {
  top: 5%;
  position: absolute;
  left: 115px;
  color: white;
  text-shadow: 1px 1px black;
  /*for the elipsis*/
  white-space: nowrap;
  width: 15em;
  overflow: hidden;
  text-overflow: ellipsis;
}

.doctor-card .doctor-code {
  top: 40%;
  position: absolute;
  left: 115px;
  color: white;
  text-shadow: 1px 1px black;
  /*for the elipsis*/
  white-space: nowrap;
  width: 15em;
  overflow: hidden;
  text-overflow: ellipsis;
}

